import type { RequestHandler } from "express";

import { underStudyLogin } from "./login.js";
import { UNDER_STUDY_SERVER } from "./utils.js";
import type { AuthLoginFailedResult } from "../auth/index.js";
import { LoginFailType } from "../config/loginFailTypes.js";
import type {
  AccountInfo,
  CommonFailedResponse,
  EmptyObject,
  LoginOptions,
} from "../typings.js";
import { EDGE_USER_AGENT_HEADERS } from "../utils/index.js";

export interface UnderGradeListOptions extends LoginOptions {
  /**  */
  time?: string;
}

interface RawUnderGradeResultItem {
  /**  */
  xnxqmc: string;
  /**  */
  kcmc: string;
  /**  */
  kcywmc: string;
  /**  */
  kcdlmc: string;
  /**  */
  zcj: string;
  /**  */
  zcjfs: number;
  /**  */
  ksxzmc: "" | "" | "";
  /**  */
  cjfsmc: "" | "";
  /**  */
  xf: number;
  /**  */
  zxs: number;
  /**  */
  xdfsmc: string;
  /**  */
  kkbmmc: string;
  /**  */
  cjbzmc: string;

  /**  */
  xnxqdm: string;
  /**  */
  kcbh: string;
  /**  */
  kcptbh: string;
  /**  */
  ksxzdm: string;
  /**  */
  kcdm: string;
  /**  */
  xdfsdm: string;
  /**  */
  cjdm: string;
  /**  */
  khfsdm: string;

  /**  */
  xsxm: string;
  /**  */
  xsbh: string;
  /**  */
  xsdm: string;

  xsckcj: "0";
  rownum_: number;
  ismax: "1" | "0";
  isactive: "1";
  wpjbz: "";
  kcflmc: "";
  cjjd: "";
  bz: "";
  xsckcjbz: "";
  kcrwdm: "";
  wzc: "0";
  wpj: "0";

  xmmc: "";
  rwdm: "";
  wzcbz: "";
}

interface RawUnderGradeSuccessResult {
  data: "";
  rows: RawUnderGradeResultItem[];
  total: number;
}

interface RawUnderGradeFailedResult {
  code: number;
  data: string;
  message: string;
}

type RawUnderGradeResult =
  | RawUnderGradeSuccessResult
  | RawUnderGradeFailedResult;

export interface UnderStudyGradeResult {
  /**  */
  time: string;
  /**  id */
  cid: string;
  /**  */
  name: string;
  /**  */
  grade: number;
  /**  */
  gradeCode: string;
  /**  */
  gradeText: string;
  gradeType: "" | "";
  /**  */
  courseType: string;
  /**  */
  shortCourseType: string;
  /**  */
  hours: number | null;
  /**  */
  point: number;
  /**  */
  mark: string;
  /**  */
  examType: "" | "" | "";
}

export interface UnderGradeListSuccessResponse {
  success: true;
  data: UnderStudyGradeResult[];
}

export type UnderGradeListResponse =
  | UnderGradeListSuccessResponse
  | AuthLoginFailedResult
  | CommonFailedResponse;

const QUERY_URL = `${UNDER_STUDY_SERVER}/new/student/xskccj/kccjDatas`;

const getGradeLists = (
  records: RawUnderGradeResultItem[],
): UnderStudyGradeResult[] =>
  records.map(
    ({
      xnxqmc,
      kcmc,
      kcdlmc,
      zcj,
      zcjfs,
      ksxzmc,
      cjfsmc,
      xf,
      zxs,
      xdfsmc,
      kkbmmc,
      cjdm,
      cjbzmc,
      kcptbh,
    }) => ({
      time: xnxqmc.replace(/^20/, "").replace(/$/, ""),
      cid: kcptbh,
      name: kcmc,
      grade: zcjfs,
      gradeCode: cjdm,
      gradeText: zcj,
      gradeType: cjfsmc,
      courseType: kcdlmc,
      shortCourseType: xdfsmc,
      office: kkbmmc,
      hours: zxs,
      point: xf,
      examType: ksxzmc,
      mark: cjbzmc,
    }),
  );

export const underStudyGradeListHandler: RequestHandler<
  EmptyObject,
  EmptyObject,
  UnderGradeListOptions
> = async (req, res) => {
  try {
    const { time = "" } = req.body;
    let cookieHeader = req.headers.cookie;

    if (!cookieHeader) {
      if (!req.body.id || !req.body.password)
        return res.json({
          success: false,
          msg: "",
        } as CommonFailedResponse);

      const result = await underStudyLogin(req.body as AccountInfo);

      if (!result.success) return res.json(result);
      cookieHeader = result.cookieStore.getHeader(QUERY_URL);
    }

    const response = await fetch(QUERY_URL, {
      method: "POST",
      headers: {
        Accept: "application/json, text/javascript, */*; q=0.01",
        Cookie: cookieHeader,
        Referer: `${UNDER_STUDY_SERVER}/new/student/xskccj/kccjList.page`,
        ...EDGE_USER_AGENT_HEADERS,
      },
      body: new URLSearchParams({
        xnxqdm: time,
        source: "kccjlist",
        primarySort: "cjdm desc",
        page: "1",
        rows: "150",
        sort: "kcmc",
        order: "asc",
      }),
    });

    if (response.headers.get("content-type")?.includes("text/html"))
      return res.json({
        success: false,
        type: LoginFailType.Expired,
        msg: "",
      });

    const data = (await response.json()) as RawUnderGradeResult;

    if ("code" in data) {
      if (data.message === "")
        return {
          success: false,
          type: LoginFailType.Expired,
          msg: "",
        };

      return {
        success: false,
        msg: data.message,
      };
    }

    const gradeList = getGradeLists(data.rows);

    return res.json({
      success: true,
      data: gradeList,
    } as UnderGradeListSuccessResponse);
  } catch (err) {
    const { message } = err as Error;

    console.error(err);
    res.json({
      success: false,
      msg: message,
    } as AuthLoginFailedResult);
  }
};
