import { CookieStore } from "@mptool/net";
import type { RequestHandler } from "express";

import type { CommonFailedResponse, EmptyObject } from "../typings.js";

export interface GradAdmissionOptions {
  name: string;
  id: string;
}

export interface GradAdmissionSuccessResponse {
  success: true;
  info: { text: string; value: string }[];
}

export type GradAdmissionResponse =
  | GradAdmissionSuccessResponse
  | CommonFailedResponse;

const getInfo = async ({
  id,
  name,
}: GradAdmissionOptions): Promise<GradAdmissionResponse> => {
  const cookieStore = new CookieStore();
  const mainPageResponse = await fetch("https://yzb.nenu.edu.cn/yjs/sslq/", {
    method: "GET",
  });

  cookieStore.applyResponse(mainPageResponse, "yzb.nenu.edu.cn");

  const mainContent = await mainPageResponse.text();

  const captchaID =
    /<input type="hidden" name="csrf_test_name" value="(.*?)" \/>/.exec(
      mainContent,
    )![1];

  const params = new URLSearchParams({
    csrf_test_name: captchaID,
    xm: name,
    zjhm: id,
  });

  console.log("Getting params", params);

  const searchUrl = "https://yzb.nenu.edu.cn/yjs/sslq_result/2023";

  const response = await fetch(searchUrl, {
    method: "POST",
    headers: {
      Cookie: cookieStore.getHeader(searchUrl),
      "Content-Type": "application/x-www-form-urlencoded",
    },
    body: params.toString(),
  });

  if (response.status !== 200)
    return {
      success: false,
      msg: "",
    };

  const content = await response.text();

  if (/<button type="submit" ><\/button>/.test(content))
    return {
      success: false,
      msg: "",
    };

  const testID =
    /<li class="label_short"><\/li>\s*<li class="bz">(.*?)<\/li>/.exec(
      content,
    )![1];

  const way =
    /<li class="label_short"><\/li>\s*<li class="bz">(.*?)<\/li>/.exec(
      content,
    )![1];

  const address =
    /<li class="label_short"><\/li>\s*<li class="bz" style="width:300px">(.*?)<\/li>/.exec(
      content,
    )![1];

  const phone =
    /<li class="label_short"><\/li>\s*<li class="bz">(.*?)<\/li>/.exec(
      content,
    )![1];

  const others =
    /<li class="label_short"><\/li>\s*<li class="bz">(.*?)<\/li>/.exec(
      content,
    )![1];

  const receiver =
    /<li class="label_short">  <\/li>\s*<li class="bz">(.*?)<\/li>/.exec(
      content,
    )![1];

  const expressId =
    /<li class="label_short"><\/li>\s*<li class="bz"><a href='http:\/\/www.ems.com.cn' target='_blank'>(.*?)<\/a><\/li>/.exec(
      content,
    )![1];

  const info = [
    {
      text: "",
      value: name,
    },
    {
      text: "",
      value: testID,
    },
    {
      text: "",
      value: way,
    },
    {
      text: "",
      value: address,
    },
    {
      text: "",
      value: phone,
    },
    {
      text: "",
      value: others,
    },
    {
      text: "",
      value: receiver,
    },
    {
      text: "",
      value: expressId,
    },
  ];

  return {
    success: true,
    info,
  };
};

export const gradAdmissionHandler: RequestHandler<
  EmptyObject,
  EmptyObject,
  GradAdmissionOptions
> = async (req, res) => {
  try {
    res.json(await getInfo(req.body));
  } catch (err) {
    const { message } = err as Error;

    console.error(err);
    res.json({
      success: false,
      msg: message,
    } as CommonFailedResponse);
  }
};
