export type ActivateOptions =
  | ActivateInfoOptions
  | ActivatePhoneSmsOptions
  | ActivateBindPhoneOptions
  | ActivateReplacePhoneOptions
  | ActivatePasswordOptions;

export const activateHandler: RequestHandler<
  EmptyObject,
  EmptyObject,
  | ActivateInfoOptions
  | ActivatePhoneSmsOptions
  | ActivateBindPhoneOptions
  | ActivateReplacePhoneOptions
  | ActivatePasswordOptions
> = async (req, res) => {
  if (req.method === "GET") {
    const cookieStore = new CookieStore();

    const response = await getImage(cookieStore);
    const cookies = cookieStore.getAllCookies().map((item) => item.toJSON());

    cookies.forEach(({ name, value, ...rest }) => {
      res.cookie(name, value, rest);
    });

    return res.json(response);
  } else {
    try {
      const options = req.body;

      if (req.headers.cookie) {
        const cookieHeader = req.headers.cookie;

        return res.json(
          options.type === "info"
            ? checkAccount(options, cookieHeader)
            : options.type === "sms"
              ? sendSms(options, cookieHeader)
              : options.type === "bind-phone"
                ? bindPhone(options, cookieHeader)
                : options.type === "replace-phone"
                  ? replacePhone(options, cookieHeader)
                  : setPassword(options, cookieHeader),
        );
      }

      return res.json({
        success: false,
        msg: "cookie ",
      } as CommonFailedResponse);
    } catch (err) {
      const { message } = err as Error;

      console.error(err);

      return res.json({
        success: false,
        msg: message,
      } as CommonFailedResponse);
    }
  }
};
