/* eslint-disable */
import type { RequestHandler } from "express";
import type { EmptyObject } from "./typings.js";

type Time = `${number}${number}:${number}${number}`;
type Date =
  `${number}${number}${number}${number}${number}${number}${number}${number}`;

interface WeatherRawData {
  air: {
    aqi: number;
    aqi_level: number;
    aqi_name: string;
    co: string;
    no2: string;
    o3: string;
    pm10: string;
    "pm2.5": string;
    so2: string;
  };

  /**  */
  alarm: {
    [props: number]: {
      /**  */
      city: string;
      /**  */
      country: string;
      /**  */
      detail: string;
      /**  */
      info: string;
      /**  */
      level_code: string;
      /**  */
      level_name: string;
      /**  */
      province: string;
      /**  */
      type_code: string;
      /**  */
      type_name: string;
      /**  */
      update_time: string;
      /**  */
      url: string;
    };
  };

  forecast_1h: Record<
    `${number}`,
    {
      degree: string;
      update_time: string;
      weather: string;
      weather_code: string;
      weather_short: string;
      wind_direction: string;
      wind_power: string;
    }
  >;

  forecast_24h: Record<
    `${number}`,
    {
      day_weather: string;
      day_weather_code: string;
      day_weather_short: string;
      day_wind_direction: string;
      day_wind_direction_code: string;
      day_wind_power: string;
      day_wind_power_code: string;
      max_degree: string;
      min_degree: string;
      night_weather: string;
      night_weather_code: string;
      night_weather_short: string;
      night_wind_direction: string;
      night_wind_direction_code: string;
      night_wind_power: string;
      night_wind_power_code: string;
      time: string;
    }
  >;

  index: {
    time: string;
    [type: string]:
      | {
          detail: string;
          info: string;
          name: string;
        }
      | string;
  };

  limit: {
    tail_number: string;
    time: string;
  };

  observe: {
    degree: string;
    humidity: string;
    precipitation: string;
    pressure: string;
    update_time: string;
    weather: string;
    weather_code: string;
    weather_short: "";
    wind_direction: string;
    wind_power: string;
  };

  rise: Record<
    number,
    {
      sunrise: Time;
      sunset: Time;
      time: Date;
    }
  >;

  tips: {
    observe: {
      [props: string]: string;
    };
  };
}

interface WeatherRawResponse {
  data: WeatherRawData;
  status: 200;
}

/**
 * 
 *
 * @param icon 
 * @param isDay 
 */
const getWeatherCode = (icon: string, isDay: boolean) =>
  (icon === "00" || icon === "01" || icon === "03" || icon === "13"
    ? isDay
      ? "day/"
      : "night/"
    : "") + icon;

const getWindDirection = (direction: string): string =>
  direction === "8"
    ? ""
    : direction === "1"
      ? ""
      : direction === "2"
        ? ""
        : direction === "3"
          ? ""
          : direction === "4"
            ? ""
            : direction === "5"
              ? ""
              : direction === "6"
                ? ""
                : direction === "7"
                  ? ""
                  : "";

export interface WeatherOptions {
  province?: string;
  city?: string;
  county?: string;
}

export interface WeatherAirInfo {
  /**  */
  aqi: number;
  /**  */
  aqiLevel: number;
  /**  */
  aqiName: string;
  co: number;
  no2: number;
  o3: number;
  pm10: number;
  pm25: number;
  so2: number;
}

interface WeatherAlarm {
  level: string;
  type: string;
  text: string;
}

/**  */
export interface WeatherForecast1H {
  /**  */
  degree: string;
  /**  */
  time: string;
  /**  */
  weatherCode: string;
}

/** 24 */
export interface WeatherForecast24H {
  /**  */
  dayWeather: string;
  /**  */
  dayWeatherCode: string;
  /**  */
  dayWeatherShort: string;
  /**  */
  maxDegree: string;
  /**  */
  minDegree: string;
  /**  */
  nightWeather: string;
  /**  */
  nightWeatherCode: string;
  /**  */
  nightWeatherShort: string;
  /**  */
  nightWindDirection: string;
  /**  */
  nightWindPower: string;
  /**  */
  weekday: string;
}

export interface WeatherHint {
  id: string;
  name: string;
  info: string;
  detail: string;
}

export interface WeatherObserveInfo {
  /**  */
  degree: string;
  /**  */
  humidity: string;
  /**  */
  precipitation: string;
  /**  */
  pressure: string;
  /**  */
  updateTime: string;
  /**  */
  weather: string;
  /**  */
  weatherCode: string;
  /**  */
  weatherShort: string;
  /**  */
  windDirection: string;
  /**  */
  windPower: string;
}

export interface WeatherRiseInfo {
  /**  */
  sunrise: string;
  /**  */
  sunset: string;
  /**  */
  time: string;
}

/**  */
export interface WeatherData {
  air: WeatherAirInfo;
  /**  */
  alarm: WeatherAlarm[];
  /**  */
  dayForecast: WeatherForecast24H[];
  /**  */
  hourForecast: WeatherForecast1H[];
  /**  */
  observe: WeatherObserveInfo;
  /**  */
  rise: WeatherRiseInfo[];
  tips: string[];
  hints: WeatherHint[];
}

const getWeather = ({ air, alarm, ...data }: WeatherRawData): WeatherData => {
  const {
    aqi,
    aqi_level: aqiLevel,
    aqi_name: aqiName,
    co,
    no2,
    o3,
    pm10,
    "pm2.5": pm25,
    so2,
  } = air;
  const {
    wind_direction: windDirection,
    weather_code: weatherCode,
    weather_short: weatherShort,
    weather,
    degree,
    wind_power: windPower,
    humidity,
    precipitation,
    pressure,
    update_time: updateTime,
  } = data.observe;

  const rise = Object.entries(data.rise)
    .sort(([keyA], [keyB]) => Number(keyA) - Number(keyB))
    .map(([, value]) => value);

  const hints = [
    ...Object.entries(data.index)
      .filter(([id]) => id !== "time")
      .map(([id, value]) => ({
        id,
        ...(<{ name: string; info: string; detail: string }>value),
      })),
    {
      id: "tailnumber",
      name: "",
      info: data.limit.tail_number,
      detail: `: ${data.limit.tail_number}`,
    },
  ];

  const hourForecast = Object.entries(data.forecast_1h)
    .sort(([keyA], [keyB]) => Number(keyA) - Number(keyB))
    .map(([, value]) => value)
    .map(({ degree, update_time: updateTime, weather_code: weatherCode }) => {
      const { sunrise, sunset } = rise.find(
        (item) => item.time === updateTime.substring(0, 8),
      )!;
      const hour = Number(updateTime.substring(8, 10));
      const sunriseHour = Number(sunrise.substring(0, 2));
      const sunsetHour = Number(sunset.substring(0, 2));
      const isDay = sunriseHour < hour && hour <= sunsetHour;

      const weather = {
        degree: `${degree}`,
        weatherCode: getWeatherCode(weatherCode, isDay),
        time: `${updateTime.substring(8, 10)}:${updateTime.substring(10, 12)}`,
      };

      if (hour === sunriseHour)
        return [
          weather,
          {
            degree: "",
            weatherCode: "rise",
            time: sunrise,
          },
        ];

      if (hour === sunsetHour)
        return [
          weather,
          {
            degree: "",
            weatherCode: "set",
            time: sunset,
          },
        ];

      return weather;
    })
    .flat();

  const dayForecast = Object.entries(data.forecast_24h)
    .sort(([keyA], [keyB]) => Number(keyA) - Number(keyB))
    .map(
      ([
        index,
        {
          day_weather: dayWeather,
          day_weather_short: dayWeatherShort,
          day_weather_code: dayWeatherCode,
          day_wind_direction: dayWindDirection,
          day_wind_power: dayWindPower,
          night_weather: nightWeather,
          night_weather_code: nightWeatherCode,
          night_weather_short: nightWeatherShort,
          night_wind_direction: nightWindDirection,
          night_wind_power: nightWindPower,
          max_degree: maxDegree,
          min_degree: minDegree,
          time,
        },
      ]) => ({
        date: `${time.substring(5, 7)}/${time.substring(8, 10)}`,
        weekday:
          index === "0"
            ? ""
            : index === "1"
              ? ""
              : index === "2"
                ? ""
                : index === "3"
                  ? ""
                  : `${
                      ["", "", "", "", "", "", ""][
                        (new Date().getDay() + Number(index) - 1) % 7
                      ]
                    }`,
        dayWeather,
        dayWeatherShort,
        dayWeatherCode: getWeatherCode(dayWeatherCode, true),
        dayWindPower,
        dayWindDirection,
        nightWeather,
        nightWeatherShort,
        nightWeatherCode: getWeatherCode(nightWeatherCode, false),
        nightWindPower,
        nightWindDirection,
        maxDegree,
        minDegree,
      }),
    );

  const tips = Object.values(data.tips.observe || {});

  return {
    air: {
      aqi,
      aqiLevel,
      aqiName,
      co: Number(co),
      no2: Number(no2),
      o3: Number(o3),
      pm10: Number(pm10),
      pm25: Number(pm25),
      so2: Number(so2),
    },
    alarm: Object.entries(alarm).map(
      ([, { detail, level_name: level, type_name: type }]) => ({
        level,
        type,
        text: detail,
      }),
    ),
    dayForecast,
    hourForecast,
    hints,
    observe: {
      weatherCode,
      weatherShort,
      weather,
      degree,
      windDirection: getWindDirection(windDirection),
      windPower,
      humidity,
      precipitation,
      pressure,
      updateTime,
    },
    rise,
    tips,
  };
};

export const weatherHandler: RequestHandler<
  EmptyObject,
  EmptyObject,
  WeatherOptions
> = async (req, res) => {
  const { province = "", city = "", county = "" } = req.body;

  const weatherResponse = await fetch(
    `https://wis.qq.com/weather/common?source=pc&weather_type=observe|rise|forecast_1h|forecast_24h|index|alarm|limit|tips&province=${province}&city=${city}&county=${county}`,
  );
  const airResponse = await fetch(
    `https://wis.qq.com/weather/common?source=pc&weather_type=observe|rise|air|forecast_1h|forecast_24h|index|alarm|limit|tips&province=${province}&city=${city}`,
  );

  const rawData = {
    ...(<WeatherRawResponse>await weatherResponse.json()).data,
    ...(<WeatherRawResponse>await airResponse.json()).data,
  };

  return res.json(getWeather(rawData));
};
