import type { RequestHandler } from "express";

import { getAction } from "./action.js";
import { gradSystemLogin } from "./login.js";
import { MAIN_URL, SERVER } from "./utils.js";
import type { AuthLoginFailedResult } from "../auth/index.js";
import type {
  AccountInfo,
  CommonFailedResponse,
  EmptyObject,
  LoginOptions,
} from "../typings.js";

const TITLE_REG_EXP = /aField\s?="(.*?)"\.split\("\t"\);/;
const VALUE_REG_EXP = /aDataLS\s?="(.*?)"\.split\("\t"\);/;

export interface GradStudentInfo {
  /**  */
  name: string;
  /**  */
  gender: "" | "";
  /**  */
  idCard: string;
  /**  */
  politicalType: string;
  /**  */
  birth: string;
  /**  */
  people: string;

  /**  */
  id: number;
  /**  */
  grade: number;
  /**  */
  school: string;
  /**  */
  major: string;
  /**  */
  majorCode: number;
  /**  */
  type: string;
  /**  */
  category: string;
  /**  */
  inDate: string;
}

const getInfo = (content: string): GradStudentInfo => {
  const titles = TITLE_REG_EXP.exec(content)![1].split("\t");
  const values = VALUE_REG_EXP.exec(content)![1].split("\t");

  const nameIndex = titles.findIndex((title) => title === "");
  const genderIndex = titles.findIndex((title) => title === "");
  const idCardIndex = titles.findIndex((title) => title === "");
  const idCard = values[idCardIndex];
  const birth = idCard
    .substring(6, 14)
    .replace(/(\d{4})(\d{2})(\d{2})/, "$1-$2-$3");
  const peopleIndex = titles.findIndex((title) => title === "");
  const politicalTypeIndex = titles.findIndex((title) => title === "");

  const idIndex = titles.findIndex((title) => title === "");
  const id = values[idIndex];
  const schoolIndex = titles.findIndex((title) => title === "");
  const majorIndex = titles.findIndex((title) => title === "");
  const majorCodeIndex = titles.findIndex((title) => title === "");
  const typeIndex = titles.findIndex((title) => title === "");
  const categoryIndex = titles.findIndex((title) => title === "");
  const inDateIndex = titles.findIndex((title) => title === "");
  const inDate = values[inDateIndex];

  return {
    name: values[nameIndex],
    gender: values[genderIndex] as "" | "",
    idCard,
    people: values[peopleIndex],
    politicalType: values[politicalTypeIndex],
    birth,

    id: Number(id),
    grade: Number(id.substring(0, 4)),
    school: values[schoolIndex],
    major: values[majorIndex],
    majorCode: Number(values[majorCodeIndex]),
    type: values[typeIndex],
    category: values[categoryIndex],
    inDate: inDate.replace(/(\d{4})(\d{2})(\d{2})/, "$1-$2-$3"),
  };
};

export interface InfoSuccessResponse {
  success: true;
  info: GradStudentInfo;
}

export type InfoResponse =
  | InfoSuccessResponse
  | AuthLoginFailedResult
  | CommonFailedResponse;

export const getGradInfo = async (
  cookieHeader: string,
): Promise<InfoResponse> => {
  const result = await getAction(cookieHeader);

  if (result.success) {
    const { id } = result.actions.find(
      ({ name }) =>
        name === "" || name === "",
    )!;

    const response = await fetch(
      `${SERVER}/HProg/sys/php/MainRun.php?WorkName=&WorkCode=${id}`,
      {
        headers: {
          Cookie: cookieHeader,
          Referer: MAIN_URL,
        },
      },
    );

    const content = await response.text();

    if (content.includes(":!"))
      return {
        success: false,
        msg: "",
      };

    const info = getInfo(content);

    return {
      success: true,
      info,
    } as InfoSuccessResponse;
  }

  return {
    success: false,
    msg: "",
  };
};

export const gradInfoHandler: RequestHandler<
  EmptyObject,
  EmptyObject,
  LoginOptions
> = async (req, res) => {
  try {
    let cookieHeader = req.headers.cookie;

    if (!cookieHeader) {
      if (!req.body.id || !req.body.password)
        return res.json({
          success: false,
          msg: "",
        } as CommonFailedResponse);

      const result = await gradSystemLogin(req.body as AccountInfo);

      if (!result.success) return res.json(result);

      cookieHeader = result.cookieStore.getHeader(SERVER);
    }

    return res.json(await getGradInfo(cookieHeader));
  } catch (err) {
    const { message } = err as Error;

    console.error(err);
    res.json({
      success: false,
      msg: message,
    } as AuthLoginFailedResult);
  }
};
